import React from 'react';
import Swal from 'sweetalert2';

import Logout from '../Logout';

const Header = ({ isAuthenticated, setIsAdding, setIsAuthenticated, setShowLogin, searchQuery, setSearchQuery, courtName }) => {
  const handleResetAll = () => {
    Swal.fire({
      icon: 'warning',
      title: 'Reset All Court Data?',
      text: 'This will clear all court occupancy data and reset all courts to empty. This action cannot be undone!',
      showCancelButton: true,
      confirmButtonText: 'Yes, reset all!',
      cancelButtonText: 'Cancel',
      confirmButtonColor: '#d33',
      // apply our muted-button styles to the cancel button
      customClass: { cancelButton: 'muted-button' },
      // let our CSS control button appearance
      buttonsStyling: false,
    }).then((result) => {
      if (result.isConfirmed) {
        // Clear localStorage
        localStorage.removeItem('courts_data');
        
        Swal.fire({
          icon: 'success',
          title: 'Reset Complete!',
          text: 'All court data has been reset successfully.',
          timer: 2000,
          showConfirmButton: false,
        });
        
        // Refresh the page to reload default data
        setTimeout(() => {
          window.location.reload();
        }, 2000);
      }
    });
  };
  return (
    <header style={{ marginTop: '20px' }}>
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        flexWrap: 'wrap',
        gap: '16px'
      }}>
        <h1 style={{ margin: 0 }}>
          <img
            src={process.env.PUBLIC_URL + '/Tennis_Now_Logo.png'}
            alt="Court In Use logo"
            style={{ height: 70, width: 'auto', display: 'inline-block' }}
          />
          <span> Court In Use</span>
        </h1>

        {/* Search bar - hidden when viewing a specific court URL */}
        {!courtName && (
          <div style={{ 
            display: 'flex', 
            gap: 8, 
            alignItems: 'center',
            flex: '1 1 300px',
            minWidth: '200px',
            maxWidth: '500px'
          }}>
            <input
              aria-label="Search courts"
              placeholder="Search courts..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              style={{ 
                padding: '8px 10px', 
                width: '100%',
                fontSize: '14px'
              }}
            />
            {searchQuery && (
              <button
                onClick={() => setSearchQuery('')}
                className="button muted-button"
                style={{ padding: '8px 10px', whiteSpace: 'nowrap' }}
              >
                Clear
              </button>
            )}
          </div>
        )}

        <div className="admin-login-container">
          {!isAuthenticated ? (
            <button 
              onClick={() => setShowLogin(true)}
              className="button"
              style={{ fontSize: '16px', padding: '16px 16px', whiteSpace: 'nowrap' }}
            >
              Admin Login
            </button>
          ) : (
            <Logout setIsAuthenticated={setIsAuthenticated} />
          )}
        </div>
      </div>
      <style>{`
        @media (max-width: 768px) {
          .admin-login-container {
            display: none !important;
          }
        }
      `}</style>
      {isAuthenticated && (
        <div style={{ marginTop: '30px', marginBottom: '18px', display: 'flex', gap: '12px', alignItems: 'center' }}>
          <button onClick={() => setIsAdding(true)}>Add Court</button>
          <button 
            onClick={handleResetAll}
            className="muted-button"
          >
            Reset All Courts
          </button>
        </div>
      )}
    </header>
  );
};

export default Header;
