import React, { useState } from 'react';
import Swal from 'sweetalert2';

const Edit = ({ courts, selectedCourt, setCourts, setIsEditing }) => {
  const id = selectedCourt.id;

  const [courtName, setCourtName] = useState(selectedCourt.courtName);
  const [address, setAddress] = useState(selectedCourt.address);
  const [capacity, setcapacity] = useState(selectedCourt.capacity);


  const handleUpdate = e => {
    e.preventDefault();

    if (!courtName || !address || !capacity) {
      return Swal.fire({
        icon: 'error',
        title: 'Error!',
        text: 'All fields are required.',
        showConfirmButton: true,
      });
    }

    const currCourt = {
      id,
      courtName,
      address,
      capacity,
    };

    for (let i = 0; i < courts.length; i++) {
      if (courts[i].id === id) {
        courts.splice(i, 1, currCourt);
        break;
      }
    }

    localStorage.setItem('courts_data', JSON.stringify(courts));
    setCourts(courts);
    setIsEditing(false);

    Swal.fire({
      icon: 'success',
      title: 'Updated!',
      text: `${currCourt.courtName} ${currCourt.address}'s data has been updated.`,
      showConfirmButton: false,
      timer: 1500,
    });
  };

  return (
    <div className="small-container">
      <form onSubmit={handleUpdate}>
        <h1>Edit Court</h1>
        <label htmlFor="courtName">Court Name</label>
        <input
          id="courtName"
          type="text"
          name="courtName"
          value={courtName}
          onChange={e => setCourtName(e.target.value)}
        />

        <label htmlFor="address">Address</label>
        <input
          id="address"
          type="text"
          name="address"
          value={address}
          onChange={e => setAddress(e.target.value)}
        />

        <label htmlFor="capacity">Capacity</label>
        <input
          id="capacity"
          type="number"
          name="capacity"
          value={capacity}
          onChange={e => setcapacity(e.target.value)}
        />

        <div style={{ marginTop: '30px' }}>
          <input type="submit" value="Update" />
          <input
            style={{ marginLeft: '12px' }}
            className="muted-button"
            type="button"
            value="Cancel"
            onClick={() => setIsEditing(false)}
          />
        </div>
      </form>
    </div>
  );
};

export default Edit;
