import React, { useState } from 'react';

const Table = ({ courts, isAuthenticated, handleEdit, handleDelete, handleToggleOccupied, activities, activeTimes }) => {
  const [expanded, setExpanded] = useState({});

  const toggleExpand = (e, id) => {
    // Ignore clicks on interactive elements so buttons still work
    const tag = e.target && e.target.tagName ? e.target.tagName.toLowerCase() : '';
    if (['button', 'a', 'input'].includes(tag)) return;
    setExpanded(prev => ({ ...prev, [id]: !prev[id] }));
  };


  return (
    <div className="contain-table">
      <table className="striped-table">
        <thead>
          <tr>
            <th></th>
            <th>Name</th>
            <th>Action</th>
            <th className="text-center">Occupied</th>
            <th>Last Active</th>
            {isAuthenticated && (
              <th colSpan={2} className="text-center">
                Management
              </th>
            )}
          </tr>
        </thead>
        <tbody>
          {courts.length > 0 ? (
            courts.map((court, i) => (
              <>
                <tr key={court.id} onClick={(e) => toggleExpand(e, court.id)} style={{ cursor: 'pointer' }}>
                  <td>
                    <span aria-hidden="true" style={{ fontSize: '0.7em' }}>{expanded[court.id] ? '' : ''}</span>
                  </td>
                  <td>{court.name}</td>
                <td>
                  {(() => {
                    // Hide all 'I'm here' buttons if any timer is active
                    let timers = {};
                    try {
                      timers = JSON.parse(localStorage.getItem('courtTimers') || '{}');
                    } catch {}
                    const anyActive = Object.values(timers).some(expireTime => expireTime > Date.now());
                    const expireTime = timers[court.id];
                    if (anyActive) {
                      return null;
                    }
                    if (!expireTime || Date.now() > expireTime) {
                      return (
                        <button
                          onClick={(e) => { e.stopPropagation(); handleToggleOccupied(court.id); }}
                          className="button"
                          style={{ fontSize: '15px', padding: '6px 10px' }} //height, length
                        >
                          I'm here
                        </button>
                      );
                    } else {
                      return null;
                    }
                  })()}
                </td>
                <td className="text-center">{`${court.occupied}/${court.capacity}`}</td>
                <td>
                  {activeTimes && activeTimes[court.id] && activeTimes[court.id].length > 0
                    ? activeTimes[court.id]
                        .map(ts => new Date(ts).toLocaleTimeString([], { hour12: false }))
                        .map((t, idx) => <span key={idx}>{t}<br /></span>)
                    : (court.time_active ? new Date(court.time_active).toLocaleTimeString([], { hour12: false }) : '')}
                </td>
                {isAuthenticated && (
                  <>
                    <td className="text-right">
                      <button
                        onClick={(e) => { e.stopPropagation(); handleEdit(court.id); }}
                        className="button muted-button"
                      >
                        Edit
                      </button>
                    </td>
                    <td className="text-left">
                      <button
                        onClick={(e) => { e.stopPropagation(); handleDelete(court.id); }}
                        className="button muted-button"
                      >
                        Delete
                      </button>
                    </td>
                  </>
                )}
              </tr>

              {/* Expanded details row */}
              {expanded[court.id] && (
                <tr key={`expanded-${court.id}`} style={{ background: '#f7faf7' }}>
                  <td colSpan={isAuthenticated ? 8 : 6} style={{ padding: 12 }}>
                    <div style={{ display: 'flex', gap: 20, flexWrap: 'wrap', alignItems: 'flex-start' }}>
                      <div style={{ minWidth: 160, maxWidth: 320 }}>
                        <strong>Address</strong>
                        <div>{court.address}</div>
                      </div>
                      <div style={{ minWidth: 120, maxWidth: 220 }}>
                        <strong>Capacity</strong>
                        <div>{court.capacity}</div>
                      </div>
                      <div style={{ minWidth: 120, maxWidth: 220 }}>
                        <strong>Night Lights</strong>
                        <div>{court.lights ? 'Yes' : 'No'}</div>
                      </div>
                      <div style={{ minWidth: 220, maxWidth: 420 }}>
                        <strong>Recent Active Times</strong>
                        <div>
                          {(activeTimes && activeTimes[court.id] && activeTimes[court.id].length > 0)
                            ? activeTimes[court.id].slice(0,5).map((ts, idx) => (
                                <div key={idx}>{new Date(ts).toLocaleString()}</div>
                              ))
                            : (court.time_actives && court.time_actives.length > 0)
                              ? court.time_actives.slice(0,5).map((ts, idx) => <div key={idx}>{new Date(ts).toLocaleString()}</div>)
                              : <div></div>
                          }
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>
              )}
            </>
            ))
          ) : (
            <tr>
              <td colSpan={4}>No courts</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
