import React, { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import { useParams } from 'react-router-dom';
import Swal from 'sweetalert2';

import { joinApi } from '../../apiBase';

import Header from './Header';
import Table from './Table';
import Add from './Add';
import Edit from './Edit';

const Dashboard = ({ isAuthenticated, setIsAuthenticated, setShowLogin }) => {
  const { courtName } = useParams(); // Get court name from URL
  const [activeTimes, setActiveTimes] = useState({});
  const [courts, setCourts] = useState([]);
  const [selectedCourt, setselectedCourt] = useState(null);
  const [isAdding, setIsAdding] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [timers, setTimers] = useState({});
  const [searchQuery, setSearchQuery] = useState('');
  const [userLocation, setUserLocation] = useState(null);

  // Use ref to store the latest fetchCourtsData function
  const fetchCourtsDataRef = useRef();
  // store timeout IDs in a ref (do not persist timeout IDs to localStorage)
  const timerIdsRef = useRef({});
  // ref to hold latest handleToggleOccupied so callbacks can call it without causing circular deps
  const handleToggleRef = useRef();

  // Get user's location on mount
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const coords = {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          };
          setUserLocation(coords);
          console.log(`User coordinates: ${coords.latitude}, ${coords.longitude}`);
        },
        (error) => {
          console.warn('Location access denied or unavailable:', error.message);
        },
        {
          enableHighAccuracy: true,  // Request GPS if available
          timeout: 100000,            // Wait up to 100 seconds
          maximumAge: 0              // Don't use cached position
        }
      );
    }
  }, []);

  useEffect(() => {
  if (userLocation) {
    console.log('User location state updated:', userLocation);
  }
  }, [userLocation]);

  const fetchCourtsData = useCallback(async () => {
    try {
      const res = await fetch(joinApi('/api/tables'));
      const data = await res.json();
      const courtsData = data.tables || [];
      
      setCourts(courtsData);
    } catch (error) {
      console.error('Failed to fetch courts data:', error);
      setCourts([]);
    }
  }, []);

  // Update the ref when fetchCourtsData changes
  useEffect(() => {
    fetchCourtsDataRef.current = fetchCourtsData;
  }, [fetchCourtsData]);

  // Fetch active times for each court
  const fetchActiveTimes = useCallback(async () => {
    try {
      const res = await fetch(joinApi('/api/tables/active-times'));
      const data = await res.json();
      // Convert array to object keyed by court id
      const timesObj = {};
      (data.activeTimes || []).forEach(court => {
        timesObj[court.id] = court.times;
      });
      setActiveTimes(timesObj);
    } catch (error) {
      console.error('Failed to fetch active times:', error);
      setActiveTimes({});
    }
  }, []);

  // Now handleExpireTimer can safely use fetchCourtsData via the ref
  const handleExpireTimer = useCallback(async id => {
    try {
      await fetch(joinApi(`/api/tables/${id}/expire-timer`), {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      // Clear the scheduled timeout (if any)
      try {
        if (timerIdsRef.current[id]) {
          clearTimeout(timerIdsRef.current[id]);
          delete timerIdsRef.current[id];
        }
      } catch (e) {
        // ignore
      }

      // Remove timer from state and localStorage using functional update
      setTimers(prev => {
        const newTimers = { ...prev };
        delete newTimers[id];
        localStorage.setItem('courtTimers', JSON.stringify(newTimers));
        return newTimers;
      });

      // Refetch courts data using the ref
      if (fetchCourtsDataRef.current) {
        await fetchCourtsDataRef.current();
      }
      
      // Refetch active times to update the "Last Active" column
      await fetchActiveTimes();

      // Find the court name by id
      const court = courts.find(c => c.id === id);
      const name = court ? court.name : `Court ${id}`;
        const result = await Swal.fire({
          icon: 'info',
          title: 'Session Expired',
          html: `Your 45 minute session at <b>${name}</b> has expired.<br>Click <b>I'm here</b> to extend your session.`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: `I'm here`,
          cancelButtonText: 'Close',
          width: 700,
          // style cancel and confirm buttons using our app classes
          customClass: { cancelButton: 'muted-button', confirmButton: 'button' },
          // let our CSS control button appearance
          buttonsStyling: false,
        });

        if (result && result.isConfirmed) {
          try {
            // Call the same handler used for toggling occupied to extend the session
            if (handleToggleRef.current) {
              await handleToggleRef.current(id);
            }
          } catch (err) {
            console.error('Failed to extend session from popup confirm:', err);
            Swal.fire({ icon: 'error', title: 'Error', text: 'Failed to extend session.' });
          }
        }
    } catch (error) {
      console.error('Failed to expire timer:', error);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'Failed to update court occupancy.',
        showConfirmButton: false,
        timer: 2500,
      });
    }
  }, [courts, fetchActiveTimes]);
  // Keep a stable ref to the latest handleExpireTimer so mount-only effects
  // can call it without re-scheduling timeouts when the function identity changes
  const handleExpireRef = useRef(handleExpireTimer);
  useEffect(() => {
    handleExpireRef.current = handleExpireTimer;
  }, [handleExpireTimer]);


  // Load active timers from localStorage on component mount (run once)
  useEffect(() => {
    const savedTimers = JSON.parse(localStorage.getItem('courtTimers') || '{}');
    const now = Date.now();
    // Filter out expired timers
    const activeTimers = {};
    Object.entries(savedTimers).forEach(([courtId, expireTime]) => {
      if (expireTime > now) {
        activeTimers[courtId] = expireTime;
        // Set timeout for remaining time
        const remainingTime = expireTime - now;
        const timeoutId = setTimeout(() => {
          // use the ref to ensure the latest handler runs
          if (handleExpireRef.current) handleExpireRef.current(parseInt(courtId));
        }, remainingTime);
        // store the timeout id in a ref so we can clear it later
        timerIdsRef.current[courtId] = timeoutId;
      }
    });
    setTimers(activeTimers);
    localStorage.setItem('courtTimers', JSON.stringify(activeTimers));

    // Cleanup on unmount: clear any scheduled timeouts
    return () => {
      try {
        Object.values(timerIdsRef.current).forEach(timerId => clearTimeout(timerId));
      } catch (e) {}
      timerIdsRef.current = {};
    };
  }, []);

  useEffect(() => {
    fetchCourtsData();
    fetchActiveTimes();
  }, [fetchCourtsData, fetchActiveTimes]);

  // Filter courts client-side using the search query (case-insensitive) and URL parameter
  const filteredCourts = useMemo(() => {
    let filtered = courts;
    
    // Filter by URL parameter (e.g., /Glendale shows only Glendale court)
    if (courtName) {
      filtered = filtered.filter(court => {
        const name = (court.name || court.courtName || '').toString().toLowerCase();
        return name.toLowerCase().includes(courtName.toLowerCase());
      });
    }
    
    // Filter by search query
    const q = (searchQuery || '').trim().toLowerCase();
    if (q) {
      filtered = filtered.filter(court => {
        const name = (court.name || court.courtName || '').toString().toLowerCase();
        const address = (court.address || '').toString().toLowerCase();
        const capacity = court.capacity ? String(court.capacity) : '';
        return name.includes(q) || address.includes(q) || capacity.includes(q);
      });
    }
    
    return filtered;
  }, [courts, searchQuery, courtName]);

  useEffect(() => {
    return () => {
      // Clear any scheduled timeouts on unmount
      try {
        Object.values(timerIdsRef.current).forEach(timerId => clearTimeout(timerId));
      } catch (e) {}
      timerIdsRef.current = {};
    };
  }, []);

  const handleEdit = id => {
    const [court] = courts.filter(court => court.id === id);
    setselectedCourt(court);
    setIsEditing(true);
  };

  const handleDelete = id => {
    Swal.fire({
      icon: 'warning',
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      customClass: { cancelButton: 'muted-button' },
      buttonsStyling: false,
    }).then(result => {
      if (result.value) {
        const [court] = courts.filter(court => court.id === id);

        Swal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: `${court.courtName} ${court.address}'s data has been deleted.`,
          showConfirmButton: false,
          timer: 1500,
        });

        const courtsCopy = courts.filter(court => court.id !== id);
        localStorage.setItem('courts_data', JSON.stringify(courtsCopy));
        setCourts(courtsCopy);
      }
    });
  };

  const handleToggleOccupied = useCallback(async id => {
    const court = courts.find(court => court.id === id);
    if (!court) return;

    try {
      // Increment occupied value via backend
      const response = await fetch(joinApi(`/api/tables/${id}/occupied`), {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      if (!response.ok) {
        throw new Error('Failed to update occupancy');
      }

      // Set timer for this court (2 minutes = 120000 ms)
      const expireTime = Date.now() + 10 * 1000; // 10 seconds from now
      const newTimers = { ...timers, [id]: expireTime };
      
      localStorage.setItem('courtTimers', JSON.stringify(newTimers));
      setTimers(newTimers);

      // Set timeout to automatically decrement after 2 minutes
      // Set timeout to automatically decrement after 2 minutes
      // Clear existing timeout for this court if present
      if (timerIdsRef.current[id]) {
        clearTimeout(timerIdsRef.current[id]);
      }
      const timeoutId = setTimeout(() => {
        handleExpireTimer(id);
      }, 10 * 1000);
      // Store timeout ID in ref
      timerIdsRef.current[id] = timeoutId;
      
      // Refetch updated data using the ref
      if (fetchCourtsDataRef.current) {
        await fetchCourtsDataRef.current();
      }
      await fetchActiveTimes();
      
      Swal.fire({
        icon: 'success',
        title: 'Updated!',
        text: `${court.name} occupancy increased for 45 minutes.`,
        showConfirmButton: false,
        timer: 2500,
      });
      
    } catch (err) {
      console.error('Error updating occupancy:', err);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'Failed to update occupancy. Please try again.',
        showConfirmButton: true,
      });
    }
  }, [courts, timers, handleExpireTimer, fetchActiveTimes]);

  // Keep a ref to the latest handleToggleOccupied so other callbacks can call it
  useEffect(() => {
    handleToggleRef.current = handleToggleOccupied;
  }, [handleToggleOccupied]);

  return (
    <div className="container">
      {!isAdding && !isEditing && (
        <>
          <Header
            isAuthenticated={isAuthenticated}
            setIsAdding={setIsAdding}
            setIsAuthenticated={setIsAuthenticated}
            setShowLogin={setShowLogin}
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            courtName={courtName}
          />
          <Table
            courts={filteredCourts}
            isAuthenticated={isAuthenticated}
            handleEdit={handleEdit}
            handleDelete={handleDelete}
            handleToggleOccupied={handleToggleOccupied}
            timers={timers}
            activeTimes={activeTimes}
          />
        </>
      )}
      {isAdding && (
        <Add
          courts={courts}
          setCourts={setCourts}
          setIsAdding={setIsAdding}
        />
      )}
      {isEditing && (
        <Edit
          courts={courts}
          selectedCourt={selectedCourt}
          setCourts={setCourts}
          setIsEditing={setIsEditing}
        />
      )}
    </div>
  );
};

export default Dashboard;