import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

import Login from '../Login';
import Dashboard from '../Dashboard';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [showLogin, setShowLogin] = useState(false);

  useEffect(() => {
    const authStatus = JSON.parse(localStorage.getItem('is_authenticated'));
    setIsAuthenticated(authStatus || false);
  }, []);

  if (showLogin && !isAuthenticated) {
    return <Login setIsAuthenticated={setIsAuthenticated} setShowLogin={setShowLogin} />;
  }

  return (
    <Router>
      <Routes>
        <Route path="/" element={
          <Dashboard 
            isAuthenticated={isAuthenticated} 
            setIsAuthenticated={setIsAuthenticated} 
            setShowLogin={setShowLogin} 
          />
        } />
        <Route path="/:courtName" element={
          <Dashboard 
            isAuthenticated={isAuthenticated} 
            setIsAuthenticated={setIsAuthenticated} 
            setShowLogin={setShowLogin} 
          />
        } />
      </Routes>
    </Router>
  );
};

export default App;